.section-main {
  position: relative;
  background-image: url('../img/section-main-bg.jpg');
  background-repeat: no-repeat;
  height: 100vh;
  min-height: 900px;
  background-position: center center;
  background-size: cover;
  margin-top: -116px;
  padding-top: 116px;
  overflow: hidden;
  .container {
    height: 100%;
  }
  &-block {
    display: flex;
    height: 100%;
  }
  &-bottom {
    position: absolute;
    content: '';
    right: 0;
    bottom: 0;
    width: 40%;
    max-width: 765px;
    background-color: $white;
    padding-top: 64px;
    padding-left: 3.5%;
    padding-right: 6%;
    // transition: $cubic;
    &-block {
      padding-top: 54px;
      padding-bottom: 54px;
      border-top: 1px solid $secondary;
      border-bottom: 1px solid $secondary;
    }
    &-label {
      margin-bottom: 24px;
    }
    &-content {
      display: flex;
      justify-content: space-between;
      text-decoration: none;
      p {
        font-size: 28px;
        line-height: 38px;
        color: $secondary;
        width: 100%;
        max-width: 375px;
        margin-right: 10px;
      }
    }
  }
  &-middle {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
  }
  &-photo {
    display: flex;
    justify-content: center;
    width: calc(44.3% - 25px);
  }
  &-content {
    width: calc(55.7% - 25px);
    padding-top: 8%;
    height: 100%;
    h1 {
      font-weight: 600;
      font-size: 44px;
      line-height: 64px;
      color: $secondary;
      max-width: 807px;
      width: 100%;
      margin-bottom: 48px;
    }
  }
}
.services {
  padding-top: 160px;
  padding-bottom: 80px;
  &-grid.grid.type-1 {
    .services-grid-item {
      // min-height: 517px;
      border: 1px solid transparent;
      transition: $cubic;
      .grid-item-block {
        display: flex;
        flex-direction: column;
        height: 100%;
      }
      &-top {
        margin-bottom: 10px;
        &-icon {
          margin-bottom: 50px;
        }
        &-text {
          &__title {
            display: block;
            width: fit-content;
            font-size: 28px;
            line-height: 38px;
            color: $secondary;
            margin-bottom: 26px;
            text-decoration: none;
          }
        }
      }
      &-bottom {
        display: flex;
        justify-content: flex-end;
        margin-top: auto;
      }
    }
    .services-grid-item:nth-child(even) {
      background-color: $tertiary;
    }
    .services-grid-item:first-child {
      .grid-item-block {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 100%;
      }
    }
    .services-grid-item:not(:first-child) {
      padding: 12.4%;
    }
  }
}
.evidence {
  padding-top: 160px;
  padding-bottom: 170px;
  background-color: $tertiary;
  h2 {
    margin-bottom: 84px;
  }
  &-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 60px;
    &-item {
      &__title {
        font-weight: 600;
        font-size: 44px;
        line-height: 72px;
        color: $primary;
        margin-bottom: 32px;
      }
      &__desc {
        font-size: 28px;
        line-height: 46px;
        color: $secondary;
      }
    }
  }
}
.team {
  padding-top: 80px;
  padding-bottom: 160px;
  h2 {
    margin-bottom: 85px;
  }
  &-grid.grid.type-1 {
    .team-grid-item.wrap-icon-hover {
      position: relative;
      background-color: $secondary;
      border: 1px solid transparent;
      transition: $cubic;
      .team-grid-item-img {
        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }
      }
      .team-grid-item-content {
        display: flex;
        flex-direction: column;
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: $default;
        padding: 12%;
        background-color: $white;
        &__name {
          font-size: 28px;
          line-height: 36px;
          color: $secondary;
          margin-bottom: 24px;
        }
        &__label {
          margin-bottom: 34px;
        }
        &__text {
          font-size: 18px;
          line-height: 34px;
          color: $secondary;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        &-top {
          margin-bottom: 20px;
        }
        &-links {
          margin-top: auto;
          display: flex;
          align-items: center;
          justify-content: space-between;
          & > a:first-child {
            text-decoration: none;
            margin-right: 10px;
            font-size: 22px;
            line-height: 34px;
            color: $primary;
            // border-bottom: 1px solid $primary;
          }
        }
      }
    }
    .team-grid-item:last-child {
      .grid-item-block {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        a {
          font-size: 36px;
          line-height: 52px;
          color: $secondary;
          text-decoration: none;
          width: 100%;
          max-width: 240px;
          span {
            color: $primary;
          }
        }
      }
    }
  }
}
.subscription {
  position: relative;
  background-image: url('../img/subscription-bg.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;
  &-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 124px;
    padding-bottom: 124px;
  }
  &-item {
    width: 50%;
    &__title {
      font-weight: 600;
      font-size: 44px;
      line-height: 71px;
      color: $secondary;
      span {
        color: $primary;
        white-space: nowrap;
      }
    }
  }
  &-item:last-child {
    display: flex;
    justify-content: center;
  }
  &-form {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    max-width: 520px;
    .input-wrap {
      flex-grow: 1;
    }
    .btn {
      margin-left: -30px;
    }
  }
}
